            v-model = "datosPDF.modOrganizador"
            color="orange darken-1"
          ></v-text-field>
        </v-col>
        <v-col cols="12"  class="ma-0 pt-0 pb-0">
            <v-text-field
            class="ma-0 pa-0"
            label=Cantidad
            clearable 
            v-model = "datosPDF.cantidad"
            color="orange darken-1"
          ></v-text-field>
        </v-col>
        <v-col cols="12"  class="ma-0 pt-0 pb-0">
            <v-text-field
            class="ma-0 pa-0"
