          <v-menu
          v-model="menu1"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="computedFechaEntrega"
              label="Fecha de entrega"
              
              persistent-hint
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date2" no-title @input="menu1 = false"></v-date-picker>
        </v-menu>
        
        </v-col>
        <v-col cols="12"  class="ma-0 pt-0 pb-0">
            <v-text-field
            class="ma-0 pa-0"
            label="Descripción"
            clearable 
            v-model = "datosPDF.descripcion"
            color="orange darken-1"
          ></v-text-field>
        </v-col>

    <button class="BotonPDF" @click="crearPDF">Crear PDF</button>
  </v-container>
  </v-container>
