<script>
  import jsPDF from "jspdf";
  export default {
    name: 'HelloWorld',

    data:(vm) => {
      return {
         date: new Date().toISOString().substr(0, 10),
         date2: new Date().toISOString().substr(0, 10),
        dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
        fechaEntrega: vm.formatDate(new Date().toISOString().substr(0, 10)),
        menu1: false,
        menu2: false,
          imgsPDF : {
            pag1: "data:image/jpeg;base64,",
           },
          datosPDF : {
            fechaPedido : '',
            pedidoNum: '',
            modOrganizador : '',
            cantidad: '',
            precio: '',
            anticipo: '',
            pagoConEntrega: '',
            lugarEntrega : '',
            fechaEntrega : '',
            descripcion: '',
            nombreUsuario: ''
          }
        }
      


    },
    computed: {
      computedDateFormatted () {
        return this.formatDate(this.date)
      },
      computedFechaEntrega () {
        return this.formatDate(this.date2)
      },
    },
    watch: {
      date () {
        this.dateFormatted = this.formatDate(this.date)
      },
      date2 () {
        this.dateFormatted = this.formatDate(this.date2)
      },
    },
    methods:{
      crearPDF(){
        const doc = new jsPDF('landscape');
        doc.addImage(this.imgsPDF.pag1, 'JPEG', 0, 0, 300, 210);
        doc.setFontSize(10);
        doc.text(231, 54,  this.computedDateFormatted);
        doc.text(63, 79,  this.datosPDF.pedidoNum);
        doc.text(135, 79,  this.datosPDF.modOrganizador);
        doc.text(231, 79,  this.datosPDF.cantidad);
        doc.text(63, 89.5,  this.datosPDF.precio);
        doc.text(135, 89.5,  this.datosPDF.anticipo);
        doc.text(231, 89.5,  this.datosPDF.pagoConEntrega);
        doc.text(93, 99,  this.datosPDF.lugarEntrega);
        doc.text(93, 110,  this.computedFechaEntrega);
        doc.text(93, 123,  this.datosPDF.descripcion);
        doc.text(63, 165,  this.datosPDF.nombreUsuario);
        doc.setFontSize(2);
          // for(let i=0; i<=300; i+=3){
          //     for(let j=0; j<=210; j+=3){
          //         doc.text(i,j,`(${i},${j})`);
          //     }
          // }
        doc.save(this.datosPDF.nombreUsuario);
        //CUADRÍCULA PARA COORDENADAS PDF
         
      },
      formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (date) {
        if (!date) return null

        const [month, day, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
      formatDate2 (date2) {
        if (!date2) return null

        const [year, month, day] = date2.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate2 (date2) {
        if (!date2) return null

        const [month, day, year] = date2.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
    },
    
  }
</script>
